<template>
  <div>
    <v-toolbar color="grey lighten-2">
      <v-icon dark right color="primary"> mdi-plus-circle </v-icon>
    </v-toolbar>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step :complete="e1 > 1" step="1">
          
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 2" step="2">  </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step step="3">  </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card class="mb-12">
            <div style="overflow-y: auto">
              <!-- <v-treeview
                    v-model="activeNode"
                    @update:active="updateActiveNode"
                    @update:open="select"
                    activatable
                    color="warning"
                    :items="items"
                  ></v-treeview> -->
              <v-text-field
                v-model="createProcessForm.cwd"
                name=""
                label=""
              ></v-text-field>
            </div>
          </v-card>
          <v-btn color="primary" @click="e1 = 2">  </v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card class="mb-12"
            ><v-text-field
              v-model="createProcessForm.cmd"
              name=""
              label=""
            ></v-text-field
          ></v-card>

          <v-btn color="primary" @click="e1 = 3">  </v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <v-card class="mb-12" height="200px">
            <v-text-field
              v-model="createProcessForm.name"
              name=""
              label=""
            ></v-text-field>
            <v-select v-model="createProcessForm.termType" :items="['pty','std']" label=""></v-select>
            <v-row>
              <v-col cols="12" md="4">
                <v-switch
                  v-model="createProcessForm.autoRestart"
                  label=""
                  color="blue"
                  hide-details
                ></v-switch
              ></v-col>

              <v-col cols="12" md="4">
                <v-switch
                  v-model="createProcessForm.push"
                  label=""
                  color="blue"
                  hide-details
                ></v-switch
              ></v-col>

              <v-col cols="12" md="4">
                <v-switch
                  v-model="createProcessForm.logReport"
                  label=""
                  color="blue"
                  hide-details
                ></v-switch>
              </v-col>
            </v-row>
          </v-card>

          <v-btn color="primary" @click="create">  </v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>
<script>
import { getFileList } from "../api/file";
import { postProcessConfig } from "../api/process";
export default {
  model: {
    // model
    prop: "fatherValue", // v-modelpropsfatherValue
    event: "childValueChange",
    // emitchildValueChangev-model
  },
  data() {
    return {
      tree: "",
      e1: 1,
      activeNode: [],
      items: [
        {
          id: "",
          name: "/",
          children: [{}],
        },
      ],
      createProcessForm: {
        termType: "pty",
      },
      childValue: this.fatherValue,
    };
  },

  props: {},
  created() {
    getFileList("/").then((resp) => {
      this.items[0].children = resp.data
        .filter((item) => {
          return item.isDir;
        })
        .map((item) => {
          return { id: item.name, name: item.name, children: [{}] };
        });
    });
  },
  methods: {
    childInputChange() {
      // $emitchildValueChangemodel
      this.childValue = false;
      this.$emit("childValueChange", this.childValue);
    },
    create() {
      postProcessConfig(this.createProcessForm).then((resp) => {
        if (resp.code === 0) {
          this.$message.success("");
          this.$parent.getProcessList();
        }
      });
      this.childInputChange();
    },
    updateActiveNode(node) {
      this.activeNode = node;
      if (node) {
        const parentNode = this.getParentNode(node);
        console.log("Parent Node:", parentNode);
      }
    },
    getParentNode(node) {
      // id
      return this.treeData.find((item) => {
        return item.children.some((child) => child.id === node.id);
      });
    },
    select(node) {
      if (node == "") return;
      console.log(this.activeNode);
      this.getFiles(node);
    },
    active() {
      // console.log(node);
    },
    getFiles(pathAry) {
      var target = this.items[0];
      // console.log(target);
      pathAry.forEach((element) => {
        target.children.forEach((item) => {
          // console.log(element)
          if (item.name == element) {
            target = item;
          }
        });
      });
      getFileList("/" + pathAry.filter((e) => e != "").join("/")).then(
        (resp) => {
          target.children = resp.data
            .filter((item) => {
              return item.isDir;
            })
            .map((item) => {
              return { id: item.name, name: item.name, children: [{}] };
            });
        }
      );
    },
  },
};
</script>
