<template>
  <div class="about" style="height: 100%;">
    <v-banner single-line elevation="2">
      <v-icon slot="icon" size="36">
        mdi-cog
      </v-icon>
      
    </v-banner>
    <v-card class="mx-auto" max-width="600px" style="margin-top:30px;">
      <v-card-title>
        
      </v-card-title>
      <div style="margin-left: 10%;margin-right: 30%;">
        <v-text-field
          v-model="newPasswd1"
          label=""
          :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show1 ? 'text' : 'password'"
          @click:append="show1 = !show1"
          hide-details="auto"
        ></v-text-field>
        <v-text-field
          v-model="newPasswd2"
          style="margin-top: 30px;margin-bottom: 30px;"
          :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show2 ? 'text' : 'password'"
          @click:append="show2 = !show2"
          hide-details="auto"
          label=""
        ></v-text-field>
      </div>
      <v-card-actions>
        <v-btn color="blue" text @click="changePasswd">
          
        </v-btn>
        <v-spacer></v-spacer>
      </v-card-actions>
    </v-card>
    <v-card
      :loading="loading"
      class="mx-auto"
      max-width="600px"
      v-if="checkPer(0)"
      style="margin-top:30px;margin-bottom: 30px;"
    >
      <v-card-title>
        
        <v-btn
          color="primary"
          dark
          @click="esReload"
          style="margin-left: auto;"
        >
          ES
          <v-icon dark right>
            mdi-reload
          </v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text>
        <subheader>
          
        </subheader>
      </v-card-text>
      <div style="margin-left: 5%;margin-right: 10%;">
        <v-row
          :key="index"
          v-for="(item, index) in configList"
          style="max-height: 60px;"
        >
          <v-col cols="4">
            {{ item.describe }}
          </v-col>
          <v-col cols="8">
            <v-text-field
              v-if="typeof item.value != 'boolean'"
              style="max-width: 200px;"
              dense
              v-model="configForm[item.key]"
              :label="item.key"
              :hint="'' + item.default"
            ></v-text-field>
            <v-switch
              style="margin-top: 0px;"
              v-else
              v-model="configForm[item.key]"
              :label="item.key"
              :hint="'' + item.default"
            ></v-switch>
          </v-col>
          <v-col cols="8"> </v-col>
        </v-row>
      </div>
      <v-card-actions>
        <v-btn color="blue" text @click="setConfig()">
          
        </v-btn>
        <v-spacer></v-spacer>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import { changePassword } from "../api/user";
import { getConfig, setConfig, esReload } from "../api/config";
export default {
  data: () => ({
    loading: false,
    show1: false,
    show2: false,
    newPasswd1: "",
    newPasswd2: "",
    password: "Password",
    configList: [],
    configForm: {},
    rules: {
      required: (value) => !!value || "Required.",
    },
  }),
  mounted() {
    if(localStorage.getItem("role") == 0){
      this.getConfig();
    }
  },
  methods: {
    create() {},
    changePasswd() {
      if (this.newPasswd1 !== this.newPasswd2) {
        this.$message.error(``);
        return;
      }
      changePassword({
        password: this.newPasswd1,
      }).then((resp) => {
        if (resp.code === 0) {
          this.$message.success(``);
          this.newPasswd1 = "";
          this.newPasswd2 = "";
        }
      });
    },
    getConfig() {
      this.loading = true;
      getConfig().then((resp) => {
        this.configList = resp.data;
        for (let i = 0; i < this.configList.length; i++) {
          this.configForm[this.configList[i].key] = this.configList[i].value;
        }
        this.loading = false;
      });
    },
    setConfig() {
      for (var key in this.configForm) {
        this.configForm[key] = this.configForm[key] + "";
      }
      setConfig(this.configForm).then((resp) => {
        if (resp.code == 0) {
          this.$message.success("");
        }
        this.getConfig();
      });
    },
    esReload() {
      esReload().then((resp) => {
        if (resp.code == 0) {
          this.$message.success("es");
        }
      });
    },
  },
};
</script>
<style></style>
