<template>
  <div class="about" style="height: 100%;">
    <v-banner single-line elevation="2">
      <v-icon slot="icon" size="36">
        mdi-account-circle
      </v-icon>
      
      <template v-slot:actions>
        <v-btn class="ma-2" color="primary" dark @click="addDialog = true">
          
          <v-icon dark right>
            mdi-plus-circle
          </v-icon>
        </v-btn>
      </template>
    </v-banner>
    <v-data-table
      :loading="loading"
      :headers="headers"
      :items="desserts"
      sort-by="calories"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-dialog v-model="dialog" max-width="500px">
          <v-card>
            <v-card-title>
              
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-text-field
                  v-model="userForm.password"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :rules="[rules.required, rules.min]"
                  :type="show1 ? 'text' : 'password'"
                  name=""
                  label=""
                  hint="4"
                  counter
                  @click:append="show1 = !show1"
                ></v-text-field>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">
                
              </v-btn>
              <v-btn color="blue darken-1" text @click="save">
                
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5"></v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                ></v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                ></v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="addDialog" max-width="500px">
          <v-card>
            <v-card-title class="text-h5"></v-card-title>
            <v-card-text>
              <v-text-field
                v-model="addUserForm.account"
                :rules="[rules.required, rules.min]"
                name=""
                label=""
                hint="4"
                counter
                @click:append="show1 = !show1"
              ></v-text-field>
              <v-text-field
                v-model="addUserForm.password"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, rules.min]"
                :type="show1 ? 'text' : 'password'"
                name=""
                label=""
                hint="4"
                counter
                @click:append="show1 = !show1"
              ></v-text-field>
              <v-select
                v-model="addUserForm.role"
                :items="items"
                item-text="label"
                label=""
                persistent-hint
                return-object
                single-line
              ></v-select>
              <v-text-field
                v-model="addUserForm.remark"
                name=""
                label=""
                hint="4"
              ></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="addDialog = false"
                ></v-btn
              >
              <v-btn color="blue darken-1" text @click="add"></v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>
      <template v-slot:item.createTime="{ item }">
        {{ timeHanlder(item.createTime) }}
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
          :disabled="item.role != 2"
          small
          class="mr-2"
          @click="oprEdit(item)"
        >
          mdi-application-edit
        </v-icon>
        <v-icon small class="mr-2" @click="editItem(item)">
          mdi-key-change
        </v-icon>
        <v-icon small @click="deleteItem(item)">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:item.role="{ item }">
        {{ getRole(item.role) }}
      </template>
    </v-data-table>
    <v-dialog v-model="oprEditdialog" max-width="1000px">
      <v-card>
        <v-card-title>
          
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-data-table
              :headers="permissionHeaders"
              :items="oprList"
              class="elevation-1"
            >
              <template v-slot:item.actions="{ item }">
                <v-icon small class="mr-2" @click="oprEditSub(item)">
                  mdi-application-edit
                </v-icon>
              </template>
              <template v-slot:item.owned="{ item }">
                <v-icon small class="mr-2" v-if="item.owned == true">
                  mdi-check
                </v-icon>
                <v-icon small class="mr-2" v-else>
                  mdi-close
                </v-icon>
              </template>
              <template v-slot:item.start="{ item }">
                <v-icon small class="mr-2" v-if="item.start == true">
                  mdi-check
                </v-icon>
                <v-icon small class="mr-2" v-else>
                  mdi-close
                </v-icon>
              </template>
              <template v-slot:item.stop="{ item }">
                <v-icon small class="mr-2" v-if="item.stop == true">
                  mdi-check
                </v-icon>
                <v-icon small class="mr-2" v-else>
                  mdi-close
                </v-icon>
              </template>
              <template v-slot:item.terminal="{ item }">
                <v-icon small class="mr-2" v-if="item.terminal == true">
                  mdi-check
                </v-icon>
                <v-icon small class="mr-2" v-else>
                  mdi-close
                </v-icon>
              </template>
            </v-data-table>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="blue darken-1" text @click="oprEditdialog = false">
            
          </v-btn>
          <v-btn color="blue darken-1" text @click="oprEditdialog = false">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="oprEditdiaFormDialog" max-width="300px">
      <v-card style="background-color:rgb(241, 241, 232)">
        <v-card-title>
          
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-switch
              flat
              v-model="permissionEditForm.owned"
              :label="``"
            ></v-switch>
            <v-switch
              flat
              v-model="permissionEditForm.start"
              :label="``"
            ></v-switch>
            <v-switch
              flat
              v-model="permissionEditForm.stop"
              :label="``"
            ></v-switch>
            <v-switch
              flat
              v-model="permissionEditForm.terminal"
              :label="``"
            ></v-switch>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="blue darken-1"
            text
            @click="oprEditdiaFormDialog = false"
          >
            
          </v-btn>
          <v-btn color="blue darken-1" text @click="submit">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {
  getUserList,
  changePassword,
  createUser,
  getPermission,
  editPermission,
} from "../api/user";
export default {
  data: () => ({
    loading: false,
    oprEditdiaFormDialog: false,
    oprList: [],
    permissionEditForm: {},
    roleMap: {
      0: "root",
      1: "admin",
      2: "user",
    },
    oprMap: {
      : "",
      : "start",
      : "delete",
      : "terminal",
    },
    oprForm: {
      owned: false,
      start: false,
      delete: false,
      terminal: false,
    },
    account: "",
    uuid: "",
    dialog: false,
    oprEditdialog: false,
    oprEditSubDialog: false,
    addUserForm: {},
    dialogDelete: false,
    userForm: {},
    show1: false,
    oprTableData: [],
    headers: [
      {
        text: "",
        align: "start",
        sortable: false,
        value: "account",
      },
      { text: "", value: "role", sortable: false },
      { text: "", value: "remark", sortable: false },
      { text: "", value: "createTime", sortable: false },
      { text: "", value: "actions", sortable: false },
    ],
    permissionHeaders: [
      { text: "id", value: "pid", sortable: false },
      {
        text: "",
        sortable: false,
        value: "name",
      },
      { text: "", value: "owned", sortable: false },
      { text: "", value: "start", sortable: false },
      { text: "", value: "stop", sortable: false },
      { text: "", value: "terminal", sortable: false },
      { text: "", value: "actions", sortable: false },
    ],
    select: { label: "Florida", value: "FL" },
    items: [
      { label: "admin", value: 1 },
      { label: "user", value: 2 },
    ],
    desserts: [],
    editedIndex: -1,
    addDialog: false,
    editedItem: {},
    rules: {
      required: (value) => !!value || ".",
      min: (v) => v.length >= 4 || "4",
    },
    defaultItem: {
      name: "",
      calories: 0,
      fat: 0,
      carbs: 0,
      protein: 0,
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    submit() {
      this.oprEditdiaFormDialog = false;
      this.permissionEditForm.account = this.account;
      editPermission(this.permissionEditForm).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
          this.oprEdit({
            account: this.account,
            uuid: this.uuid,
          });
        }
      });
    },
    getRole(e) {
      return this.roleMap[e];
    },
    initialize() {
      this.loading = true;
      getUserList().then((resp) => {
        this.desserts = resp.data;
        this.loading = false;
      });
    },
    timeHanlder(dateString) {
      var dateObj = new Date(dateString);
      var year = dateObj.getFullYear();
      var month = ("0" + (dateObj.getMonth() + 1)).slice(-2);
      var day = ("0" + dateObj.getDate()).slice(-2);
      var hours = ("0" + dateObj.getHours()).slice(-2);
      var minutes = ("0" + dateObj.getMinutes()).slice(-2);
      var seconds = ("0" + dateObj.getSeconds()).slice(-2);
      var formattedDate =
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds;
      return formattedDate;
    },
    changeOpr(opr) {
      console.log(opr);
    },
    editItem(item) {
      this.userForm = {
        account: item.account,
      };
      this.userForm.password = "";
      this.dialog = true;
    },
    oprEdit(e) {
      this.account = e.account;
      this.uuid = e.uuid;
      this.oprEditdialog = true;
      getPermission(this.account, e.uuid).then((resp) => {
        if (resp.code != 0) {
          return;
        }
        this.oprList = resp.data;
      });
    },
    oprEditSub(e) {
      this.permissionEditForm = { ...e };
      this.oprEditdiaFormDialog = true;
    },
    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
      this.initialize();
    },
    add() {
      this.addUserForm.role =
        this.addUserForm.role.value == 0 ? 2 : this.addUserForm.role.value;
      createUser(this.addUserForm).then((resp) => {
        if (resp.code == 0) {
          this.initialize();
          this.$message.success(``);
        }
      });
      this.addDialog = false;
      this.initialize();
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      changePassword(this.userForm).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
        }
      });
      this.close();
    },
  },
};
</script>
<style scoped></style>
