import request from "@/utils/request";

export function getProcessList() {
  return request("GET", "/process", null, null);
}

export function killProcess(uuid) {
  return request("DELETE", "/process", null, { uuid: uuid });
}

export function startProcess(uuid) {
  return request("PUT", "/process", null, { uuid: uuid });
}
export function getContorl(uuid) {
  return request("GET", "/process/control", null, { uuid: uuid });
}

export function getProcessConfig(uuid) {
  return request("GET", "/process/config", null, { uuid: uuid });
}

export function deleteProcessConfig(uuid) {
  return request("DELETE", "/process/config", null, { uuid: uuid });
}

export function putProcessConfig(data) {
  return request("PUT", "/process/config", data, null);
}

export function postProcessConfig(data) {
  return request("POST", "/process/config", data, null);
}
