<template>
  <div class="about" style="height: 100%">
    <v-banner single-line elevation="2">
      <v-icon slot="icon" size="36"> mdi-information-slab-circle </v-icon>
      
      <template v-slot:actions>
        <v-btn class="ma-2" color="primary" dark @click="filter">
          
          <v-icon dark right> mdi-filter </v-icon>
        </v-btn>
        <v-btn class="ma-2" color="primary" dark @click="reset">
          
          <v-icon dark right> mdi-restart </v-icon>
        </v-btn>
      </template>
    </v-banner>
    <div v-if="this.length != 0">
      <v-row style="margin-left: 10px; margin-right: 10px; max-height: 80px">
        <v-col cols="1" sm="10" md="2">
          <v-text-field
            label=""
            prepend-icon="mdi-calendar"
            :hint="this.hintTime"
            v-model="this.startTime"
          ></v-text-field>
        </v-col>
        <v-col cols="1" sm="10" md="2">
          <v-text-field
            label=""
            :hint="this.hintTime"
            prepend-icon="mdi-calendar"
            v-model="this.endTime"
          ></v-text-field>
        </v-col>
        <v-col cols="1" sm="10" md="2">
          <v-select
            style="max-width: 140px"
            v-model="sort"
            :items="items"
            prepend-icon="mdi-sort"
            item-text="label"
            hint=""
            label=""
            return-object
            single-line
          ></v-select>
        </v-col>
        <v-col style="margin-left: auto" cols="1" sm="10" md="2">
          <v-text-field
            v-model="logForm.match.log"
            label=""
          ></v-text-field
        ></v-col>
        <v-col cols="1" sm="10" md="2">
          <v-text-field
            v-model="logForm.match.name"
            label=""
          ></v-text-field
        ></v-col>
        <v-col cols="1" sm="10" md="2">
          <v-text-field
            v-model="logForm.match.using"
            label=""
          ></v-text-field
        ></v-col>
      </v-row>
      <v-data-table
        dense
        :loading="loading"
        :headers="headers"
        :items="desserts"
        :disable-pagination="true"
        hide-default-footer
        class="elevation-1"
        style="margin-top: 5px"
        ><template v-slot:item.time="{ item }">
          {{ timestampToTime(item.time) }}
        </template>
        <template v-slot:item.log="{ item }">
          <div v-html="ansiToHtml(item.log)"></div>
        </template>
      </v-data-table>
      <div @click="getLogList" style="margin-top: auto">
        <v-pagination
          style="margin: 20px; max-height: 20px"
          v-model="page"
          :length="length"
          :total-visible="7"
        ></v-pagination>
      </div>
    </div>
    <div
      v-else
      style="
        display: flex;
        justify-content: center;
        align-items: center;
        height: auto;
        width: 100%;
      "
    >
      <div style="margin: 10%">
        <v-icon dark size="200" color="blue-grey">
          mdi-information-slab-circle
        </v-icon>
        <v-subheader></v-subheader>
      </div>
    </div>
  </div>
</template>

<script>
import { getLog } from "../api/log";
import { AnsiUp } from "ansi_up";
export default {
  data: () => ({
    createDialog: false,
    uuid: "",
    startTime: "",
    sort: { value: "desc" },
    endTime: "",
    page: 1,
    size: 22,
    hintTime: "",
    length: 1,
    loading: false,
    logForm: {
      match: {
        log: "",
        name: "",
        using: "",
      },
      page: {
        from: 0,
        size: 30,
      },
      time: {
        startTime: undefined,
        endTime: undefined,
      },
    },
    headers: [
      {
        text: "",
        sortable: false,
        width: "80%",
        value: "log",
      },
      { text: "", value: "name", sortable: false },
      { text: "", value: "using", sortable: false },
      { text: "", value: "time", sortable: false },
    ],
    items: [
      { label: "", value: "" },
      { label: "", value: "asc" },
      { label: "", value: "desc" },
    ],
    desserts: [],
    tableData: [],
  }),
  mounted() {},
  created() {
    this.getLogList();
    this.hintTime = this.formatDateTime(new Date());
  },
  methods: {
    formatDateTime(date) {
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      const hour = date.getHours();
      const minute = date.getMinutes();
      const second = date.getSeconds();
      return `${year}-${this.pad(month)}-${this.pad(day)} ${this.pad(
        hour
      )}:${this.pad(minute)}:${this.pad(second)}`;
    },
    pad(num) {
      return num.toString().padStart(2, "0");
    },
    timestampToTime(timestamp) {
      timestamp = timestamp ? timestamp : null;
      let date = new Date(timestamp); //10*1000131000
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D =
        (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
      let h =
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
      let m =
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        ":";
      let s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      return Y + M + D + h + m + s;
    },
    reset() {
      this.page = 1;
      this.logForm = {
        match: {
          log: "",
          name: "",
          using: "",
        },
        page: {
          from: 0,
          size: 30,
        },
        time: {
          startTime: undefined,
          endTime: undefined,
        },
      };
      this.getLogList();
    },
    filter() {
      this.page = 1;
      this.getLogList();
    },
    getLogList() {
      this.loading = true;
      this.logForm.time.startTime = this.timeToTimestamp(this.startTime);
      this.logForm.time.endTime = this.timeToTimestamp(this.endTime);
      this.logForm.page.from = (this.page - 1) * this.size;
      this.logForm.page.size = this.size;
      this.logForm.sort = this.sort.value;
      getLog(this.logForm).then((resp) => {
        this.desserts = resp.data.data;
        this.length = Math.ceil(resp.data.total / this.size);
        this.loading = false;
      });
    },
    ansiToHtml(ansi){
      var ansi_up = new AnsiUp();
      return ansi_up.ansi_to_html(ansi)
    },
    timeToTimestamp(time) {
      let timestamp = Date.parse(new Date(time).toString());
      //timestamp = timestamp / 1000; //131000131000
      console.log(time + "" + timestamp);
      return timestamp;
      //2021-11-18 22:14:241637244864707
    },
  },
};
</script>
<style></style>
