import request from "@/utils/request";

export function createPush(data) {
  return request("POST", "/push", data, null);
}

export function getPushList() {
  return request("GET", "/push/list", null, null);
}

export function deletePush(id) {
  return request("DELETE", "/push", null, { id: id });
}

export function getPush(id) {
  return request("GET", "/push", null, { id: id });
}

export function editPush(data) {
  return request("PUT", "/push", data, null);
}
