<template>
  <v-card class="mx-auto" max-width="90%" min-width="500px">
    <v-card-text>
      <v-icon
        color="green"
        v-if="item.state.state == 1"
        x-large
        style="float: left"
      >
        mdi-checkbox-marked-circle</v-icon
      >
      <v-icon
        color="red"
        v-if="item.state.state == 0"
        x-large
        style="float: left"
      >
        mdi-stop-circle</v-icon
      >
      <div v-if="item.state.state == 2" style="float: left">
        <v-tooltip top color="warning">
          <template v-slot:activator="{ on, attrs }">
            <v-icon color="yellow accent-4" x-large v-bind="attrs" v-on="on">
              mdi-alert-circle</v-icon
            >
          </template>
          <span>{{ item.state.info }}</span>
        </v-tooltip>
      </div>
      <div style="float: right" v-if="checkPer(1)">
        <v-menu bottom left>
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="black" dark icon v-bind="attrs" v-on="on">
              <v-icon>mdi-dots-vertical</v-icon>
            </v-btn>
          </template>

          <v-list nav dense>
            <v-list-item @click="contorl(item)">  </v-list-item>
            <v-list-item @click="deleteProcess(item)">  </v-list-item>
          </v-list>
        </v-menu>
      </div>
      <div style="display: flex; align-items: center">
        <p class="text-h4 text--primary">
          {{ item.name == "console" ? "" : item.name }}
        </p>

        <v-subheader>{{ item.user }}</v-subheader>
      </div>
      <v-divider></v-divider>
      <template>
        <div
          :id="'echarts' + index"
          :style="{ width: '100%', height: '260px' }"
        ></div>
      </template>
    </v-card-text>
    <v-card-actions>
      <v-btn icon @click="link(item)" :disabled="item.state.state != 1">
        <v-icon>mdi-console</v-icon>
      </v-btn>
      <v-btn icon @click="start(item)">
        <v-icon>mdi-play</v-icon>
      </v-btn>
      <v-btn icon @click="stop(item)">
        <v-icon>mdi-stop</v-icon>
      </v-btn>
      <v-btn icon @click="openSet(item)" v-if="checkPer(0)">
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
      <v-subheader style="margin-left: auto"> {{ item.startTime }}</v-subheader>
    </v-card-actions>
    <v-dialog
      v-model="dialogStd"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card
        style="
          height: 100%;
          background-color: black;
          display: flex;
          flex-direction: column;
        "
      >
        <v-toolbar
          dense
          dark
          color="blue-grey darken-4"
          style="height: 48px; flex-grow: 0"
        >
          <v-toolbar-title>(std)</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn icon dark @click="closeStd">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <div
          class="data-list-content"
          style="
            overflow-y: auto;
            margin-left: 10px;
            margin-right: 10px;
            flex-grow: 1;
            max-height: 90%;
          "
          ref="box"
          @click="focus"
        >
          <li
            v-for="(line, index) in outputLines"
            v-bind:key="index"
            style="list-style-type: none; font-size: 12px"
          >
            <div v-html="line"></div>
          </li>
        </div>
        <div @keyup="keyEvent" style="overflow-y: auto; display: flex">
          <li
            style="
              list-style-type: none;
              font-size: 13px;
              margin-bottom: 20px;
              margin-left: 10px;
              margin-right: 10px;
              flex-grow: 1;
              color: white;
            "
          >
            >
            <input
              class="terminalInput"
              ref="inputBox"
              v-model="input"
              style="width: 95%"
            />
          </li>
        </div>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="dialogPty"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card
        style="
          height: 100%;
          background-color: black;
          display: flex;
          flex-direction: column;
        "
      >
        <v-toolbar
          dense
          dark
          color="blue-grey darken-4"
          style="height: 48px; flex-grow: 0"
        >
          <v-toolbar-title>(pty)</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn icon dark @click="closePty">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <div style="height: 100%; background: black">
          <div :id="'xterm'+index" ref="xterm"></div>
        </div>
      </v-card>
    </v-dialog>
    <v-dialog v-model="setDialog" width="500">
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">  </v-card-title>

        <div style="margin: 10%">
          <v-text-field
            label=""
            v-model="processForm.name"
          ></v-text-field>
          <v-text-field
            label=""
            v-model="processForm.cwd"
          ></v-text-field>
          <v-text-field
            label=""
            hide-details="auto"
            v-model="processForm.cmd"
          ></v-text-field>
          <v-row>
            <v-col cols="12" md="4">
              <v-switch
                v-model="processForm.autoRestart"
                label=""
                color="blue"
                hide-details
              ></v-switch
            ></v-col>

            <v-col cols="12" md="4">
              <v-switch
                v-model="processForm.push"
                label=""
                color="blue"
                hide-details
              ></v-switch
            ></v-col>

            <v-col cols="12" md="4">
              <v-switch
                v-model="processForm.logReport"
                label=""
                color="blue"
                hide-details
              ></v-switch>
            </v-col>
          </v-row>
        </div>
        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" dark @click="setDialog = false">
            <v-icon left dark> mdi-close </v-icon>
            
          </v-btn>
          <v-btn color="primary" dark @click="set">
            <v-icon left dark> mdi-check </v-icon>
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import {
  getProcessConfig,
  putProcessConfig,
  deleteProcessConfig,
  startProcess,
  killProcess,
  getContorl,
} from "../api/process";
import "xterm/css/xterm.css";
import "xterm/lib/xterm.js";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
import { AttachAddon } from "xterm-addon-attach";
import { AnsiUp } from "ansi_up";
const gradients = [
  ["#222"],
  ["#42b3f4"],
  ["red", "orange", "yellow"],
  ["purple", "violet"],
  ["#00c6ff", "#F0F", "#FF0"],
  ["#f72047", "#ffd200", "#1feaea"],
];
export default {
  name: "",
  data() {
    return {
      state: 0,
      setDialog: false,
      padding: 8,
      input: "",
      outputLines: [],
      output: "",
      radius: 10,
      width: 2,
      lineCap: "round",
      processForm: {},
      dialogStd: false,
      dialogPty: false,
      gradient: gradients[5],
      gradientDirection: "top",
      gradients,
      fill: false,
      type: "trend",
      autoLineWidth: false,
      cmdList: [],
      cmdIndex: -1,
    };
  },
  props: {
    item: {}, //
    index: 0,
  },
  components: {},
  created() {},
  mounted() {
    this.item.usage.cpu =
      this.item.usage.cpu == null
        ? [0, 0]
        : this.item.usage.cpu.map(function (num) {
            return parseFloat(num.toFixed(2));
          });
    this.item.usage.mem =
      this.item.usage.cpu == null
        ? [0, 0]
        : this.item.usage.mem.map(function (num) {
            return parseFloat((num / 1000).toFixed(2));
          });

    var myChart = this.$echarts.init(
      document.getElementById("echarts" + this.index)
    );
    var option = {
      tooltip: {
        trigger: "axis",
      },
      legend: {
        data: ["CPU", ""],
      },
      animationDuration: 2000,
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true,
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        show: false,
        data: this.item.usage.time,
      },
      yAxis: [
        {
          type: "value",
          name: "CPU(%)",
          splitLine: {
            show: false,
          },
          axisLine: { show: false },
          axisTick: { show: false },
        },
        {
          type: "value",
          name: "(MB)",
          axisLine: { show: false },
          axisTick: { show: false },
          splitLine: { show: false },
        },
      ],
      series: [
        {
          name: "CPU",
          type: "line",
          data: this.item.usage.cpu,
          yAxisIndex: 0,
          showSymbol: false,
          lineStyle: {
            color: "#4ee5b9",
          },
          itemStyle: {
            color: "#4ee5b9",
          },
        },
        {
          name: "",
          type: "line",
          data: this.item.usage.mem,
          yAxisIndex: 1,
          showSymbol: false,
          lineStyle: {
            color: "#ffe17e",
          },
          itemStyle: {
            color: "#ffe17e",
          },
        },
      ],
    };
    myChart.setOption(option);
  },
  methods: {
    initTerm() {
      let term = new Terminal({
        rendererType: "canvas", //
        rows: parseInt(50), //
        cols: parseInt(50), // 
        convertEol: true, //
        disableStdin: false, //
        cursorBlink: true, //
        theme: {
          foreground: "#ECECEC", //
          cursor: "help", //
          lineHeight: 25,
        },
      });
      const attachAddon = new AttachAddon(this.socket);
      const fitAddon = new FitAddon();
      term.loadAddon(attachAddon);
      term.loadAddon(fitAddon);
      term.open(document.getElementById('xterm'+this.index));
      fitAddon.fit();
      term.focus();
      this.term = term;
    },
    initSocket(url) {
      this.socket = new WebSocket(url);
      this.socketOnClose();
      this.socketOnOpen();
      this.socketOnError();
    },
    socketOnClose() {
      this.socket.onclose = () => {
        this.term.dispose();
      };
    },
    socketOnError() {
      this.socket.onerror = () => {
        // console.log('socket ')
      };
    },
    socketOnOpen() {
      this.socket.onopen = () => {
        // 
        this.initTerm();
      };
    },
    start(item) {
      startProcess(item.uuid).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
          this.$parent.getProcessList();
        }
      });
    },
    stop(item) {
      killProcess(item.uuid).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
          this.$parent.getProcessList();
        }
      });
    },
    contorl(item) {
      getContorl(item.uuid).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
        }
      });
    },
    handleClose() {
      this.websock.close();
      this.dialog = false;
      this.isLink = false;
    },
    openSet(item) {
      this.uuid = item.uuid;
      this.setDialog = true;
      getProcessConfig(item.uuid).then((resp) => {
        if (resp.code === 0) {
          this.processForm = resp.data;
          this.state = item.state.state;
          console.log(this.processForm);
        }
      });
    },
    set() {
      putProcessConfig(this.processForm).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
          this.$parent.getProcessList();
        }
      });
      this.setDialog = false;
      this.processForm = {};
    },
    link(row) {
      const token = localStorage.getItem("token");
      this.outputLines = [];
      if (row.termType === "pty") {
        this.dialogPty = true;
        this.initWebSocketPty(row.uuid, token);
      } else {
        this.dialogStd = true;
        this.initWebSocketStd(row.uuid, token);
      }
    },
    focus() {
      this.$nextTick(() => {
        this.$refs.inputBox.focus();
      });
    },
    closeStd() {
      this.dialogStd = false;
      this.websock.close();
    },
    closePty() {
      this.dialogPty = false;
      this.socket.close();
    },
    keyEvent(event) {
      var keyCode = event.keyCode;
      console.log(keyCode);
      //
      if (keyCode == 13) {
        this.websocketsend(this.input);
        this.cmdList.unshift(this.input);
        this.cmdIndex = -1;
        this.input = "";
      }
      //
      if (keyCode == 38) {
        if (this.cmdIndex < this.cmdList.length) {
          this.cmdIndex++;
          this.input = this.cmdList[this.cmdIndex];
        }
      }
      //
      if (keyCode == 40) {
        if (this.cmdIndex > 0) {
          this.cmdIndex--;
          this.input = this.cmdList[this.cmdIndex];
        }
      }
    },
    deleteProcess(item) {
      deleteProcessConfig(item.uuid).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
          this.$parent.getProcessList();
        }
      });
    },
    initWebSocketStd(uuid, token) {
      //weosocket
      const baseUrl =
        "ws://" +
        window.location.hostname +
        ":" +
        window.location.port +
        "/api/ws";
      // const baseUrl = "ws://127.0.0.1:8797/api/ws";
      const url = baseUrl + "?uuid=" + uuid + "&token=" + token;
      this.websock = new WebSocket(url);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    initWebSocketPty(uuid, token) {
      //weosocket
      const baseUrl =
        "ws://" +
        window.location.hostname +
        ":" +
        window.location.port +
        "/api/ws";
      // const baseUrl = "ws://127.0.0.1:8797/api/ws";
      const url = baseUrl + "?uuid=" + uuid + "&token=" + token;
      this.initSocket(url);
    },
    websocketonopen() {
      this.isLink = true;

      this.$message.success("");
      //send
    },
    websocketonerror(e) {
      //
      this.dialogVisible = false;
      this.$message.error("");
      console.log(e);
    },
    websocketonmessage(e) {
      //
      if (this.outputLines.length > 1000) {
        this.outputLines.splice(0, 1);
      }
      var ansi_up = new AnsiUp();
      this.outputLines.push(ansi_up.ansi_to_html(e.data.replace(">....", "")));
      this.output = this.outputLines.join("\n");
      this.$nextTick(() => {
        this.$refs.box.scrollTop = this.$refs.box.scrollHeight;
      });
      this.$nextTick(() => {
        this.$refs.inputBox.focus();
      });
    },
    websocketsend(data) {
      //
      if (this.outputLines.length > 1000) {
        this.outputLines.splice(0, 1);
      }
      this.outputLines.push("> " + data);
      this.$nextTick(() => {
        this.$refs.box.scrollTop = this.$refs.box.scrollHeight;
      });
      this.websock.send(data);
    },
    websocketclose() {
      //
      this.$message.error("");
      this.isLink = false;
      this.dialogVisible = false;
      this.$parent.getProcessList();
    },
  },
};
</script>
