<template>
  <div class="about" style="height: 100%;">
    <v-banner single-line elevation="2">
      <v-icon slot="icon" size="36">
        mdi-application-brackets-outline
      </v-icon>
      
      <template v-slot:actions>
        <v-btn
          class="ma-2"
          color="primary"
          dark
          @click="createDialog = true"
          v-if="checkPer(1)"
        >
          
          <v-icon dark right>
            mdi-plus-circle
          </v-icon>
        </v-btn>
      </template>
    </v-banner>
    <div v-if="tableData.length !== 0">
      <div
        v-for="(item, key) in tableData"
        v-bind:key="key"
        style="display: flex; align-items: center;width: 33%;margin-top: 30px;float: left;min-width: 500px;"
      >
        <ProcessCard style="margin: auto;" :item="item" :index="key"></ProcessCard>
      </div>
    </div>
    <div
      v-else
      style="display: flex; justify-content: center; align-items: center; height: auto; width: 100%;"
    >
      <div style="margin: 10%;">
        <v-icon dark size="200" color="blue-grey">
          mdi-application-brackets-outline
        </v-icon>
        <v-subheader></v-subheader>
      </div>
    </div>

    <v-dialog v-model="createDialog">
      <div>
        <ProcessCreate v-model="createDialog"></ProcessCreate>
      </div>
    </v-dialog>
  </div>
</template>

<script>
import { getProcessList } from "../api/process";
import ProcessCard from "../components/ProcessCard.vue";
import ProcessCreate from "../components/ProcessCreate.vue";

export default {
  data: () => ({
    createDialog: false,
    uuid: "",

    refresh: true,
    state: "warning",
    tableData: undefined,
  }),
  components: { ProcessCard, ProcessCreate },
  mounted() {
    this.getProcessList();
    this.timer = setInterval(this.getProcessList, 3000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    create() {},

    getProcessList() {
      if (!this.refresh) {
        return;
      }
      getProcessList().then((resp) => {
        if (resp.code != 0) {
          this.refresh = false;
          return;
        }
        this.refresh = true;
        this.tableData = resp.data.sort((a, b) => a.name.localeCompare(b.name));
      });
    },
  },
};
</script>
<style>
.terminalInput {
  outline: none;
  caret-color: white;
  border: none;
  color: white;
  font-family: "Courier New", Courier, monospace;
}
.data-list-content {
  font-family: "Courier New", Courier, monospace; /*  */
  background-color: black; /*  */
  color: white; /*  */
}
</style>
