<template>
  <div class="about" style="height: 100%;">
    <v-banner single-line elevation="2">
      <v-icon slot="icon" size="36">
        mdi-message-fast
      </v-icon>
      
      <template v-slot:actions>
        <v-btn class="ma-2" color="primary" dark @click="createDialog = true">
          
          <v-icon dark right>
            mdi-plus-circle
          </v-icon>
        </v-btn>
      </template>
    </v-banner>
    <div v-if="tableData.length !== 0">
      <v-data-table
        :loading="loading"
        :headers="headers"
        :items="tableData"
        :items-per-page="10"
        class="elevation-1"
        ><template v-slot:item.enable="{ item }">
          <v-icon small class="mr-2" v-if="item.enable == true">
            mdi-check
          </v-icon>
          <v-icon small class="mr-2" v-else>
            mdi-close
          </v-icon>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editPushConfig(item)">
            mdi-application-edit
          </v-icon>
          <v-icon small @click="deletePush(item)">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </div>
    <div
      v-else
      style="display: flex; justify-content: center; align-items: center; height: auto; width: 100%;"
    >
      <div style="margin: 10%;">
        <v-icon dark size="200" color="blue-grey">
          mdi-message-fast
        </v-icon>
        <v-subheader></v-subheader>
      </div>
    </div>
    <v-dialog v-model="editDialog" max-width="500px">
      <v-card>
        <v-card-title class="text-h5"></v-card-title>
        <v-card-text>
          <v-select
            v-model="editPushForm.method"
            :items="items"
            item-text="label"
            label=""
            persistent-hint
            return-object
            single-line
          ></v-select>
          <v-text-field
            v-model="editPushForm.url"
            name="URL"
            label="URL"
            hint="{$name}- {$user}- {$messsage}- {$status}-"
          ></v-text-field>
          <v-textarea
            v-if="editPushForm.method == 'POST'"
            v-model="editPushForm.body"
            hint="{$name}- {$user}- {$messsage}- {$status}-"
            name=""
            label=""
          ></v-textarea>
          <v-text-field
            v-model="editPushForm.remark"
            name="URL"
            label=""
          ></v-text-field>
          <v-switch
            v-model="editPushForm.enable"
            name=""
            label=""
          ></v-switch>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="editDialog = false"
            ></v-btn
          >
          <v-btn color="blue darken-1" text @click="submitEdit()"></v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="createDialog" max-width="500px">
      <v-card>
        <v-card-title class="text-h5"></v-card-title>
        <v-card-text>
          <v-select
            v-model="addPushForm.method"
            :items="items"
            item-text="label"
            label=""
            persistent-hint
            return-object
            single-line
          ></v-select>
          <v-text-field
            v-model="addPushForm.url"
            name="URL"
            label="URL"
            hint="{$name}- {$user}- {$messsage}- {$status}-"
          ></v-text-field>
          <v-textarea
            v-if="addPushForm.method == 'POST'"
            v-model="addPushForm.body"
            hint="{$name}- {$user}- {$messsage}- {$status}-"
            name=""
            label=""
          ></v-textarea>
          <v-text-field
            v-model="addPushForm.remark"
            name="URL"
            label=""
          ></v-text-field>
          <v-switch
            v-model="addPushForm.enable"
            name=""
            label=""
          ></v-switch>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="createDialog = false"
            ></v-btn
          >
          <v-btn color="blue darken-1" text @click="createPushConfig()"
            ></v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import {
  createPush,
  getPushList,
  deletePush,
  getPush,
  editPush,
} from "../api/push";
export default {
  data: () => ({
    loading: false,
    createDialog: false,
    editDialog: false,
    addPushForm: {},
    editPushForm: {},
    refresh: true,
    state: "warning",
    tableData: undefined,
    items: ["POST", "GET"],
    headers: [
      {
        text: "id",
        sortable: false,
        value: "id",
      },
      { text: "", value: "method", sortable: false },
      { text: "URL", value: "url", sortable: false },
      { text: "", value: "remark", sortable: false },
      { text: "", value: "enable", sortable: false },
      { text: "", value: "actions", sortable: false },
    ],
  }),
  mounted() {
    this.getPushList();
  },
  methods: {
    createPushConfig() {
      createPush(this.addPushForm).then((resp) => {
        if (resp.code == 0) {
          this.addPushForm = {};
          this.$message.success(``);
          this.getPushList();
        }
      });

      this.createDialog = false;
    },
    getPushList() {
      this.loading = true;
      getPushList().then((resp) => {
        this.tableData = resp.data;
        this.loading = false;
      });
    },
    deletePush(item) {
      deletePush(item.id).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
          this.getPushList();
        }
      });
    },
    editPushConfig(item) {
      getPush(item.id).then((resp) => {
        this.editPushForm = resp.data;
      });
      this.editDialog = true;
    },
    submitEdit() {
      editPush(this.editPushForm).then((resp) => {
        if (resp.code == 0) {
          this.$message.success(``);
          this.getPushList();
        }
        this.editDialog = false;
      });
    },
  },
};
</script>
<style></style>
